import React from 'react';
import TableDragSelect from "react-table-drag-select";
import "react-table-drag-select/style.css";

import {
  Button,
 } from 'reactstrap';


export default class SearchTimetable extends React.Component {

  state = {
    timetable_cells_hasCourse :[
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
    ],

    timetable_cells: [
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
    ]
  };

  render = () =>
    <div className="SF">
      <TableDragSelect value={this.state.timetable_cells} onChange={this.handleChange}>
        <tr>
          <td disabled />
          <td disabled></td>
          <td disabled></td>
          <td disabled></td>
          <td disabled></td>
          <td disabled></td>
        </tr>
        <tr>
          <td disabled>09-10</td>
          <td> { this.state.timetable_cells_hasCourse[1][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[1][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[1][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[1][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[1][5] ? '' : null } </td>
        </tr>
        <tr>
          <td disabled>10-11</td>
          <td> { this.state.timetable_cells_hasCourse[2][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[2][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[2][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[2][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[2][5] ? '' : null } </td>
        </tr>
        <tr>
          <td disabled>11-12</td>
          <td> { this.state.timetable_cells_hasCourse[3][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[3][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[3][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[3][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[3][5] ? '' : null } </td>
        </tr>
        <tr>
          <td disabled>12-13</td>
          <td> { this.state.timetable_cells_hasCourse[4][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[4][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[4][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[4][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[4][5] ? '' : null } </td>
        </tr>
        <tr>
          <td disabled>13-14</td>
          <td> { this.state.timetable_cells_hasCourse[5][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[5][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[5][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[5][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[5][5] ? '' : null } </td>
        </tr>
        <tr>
          <td disabled>14-15</td>
          <td> { this.state.timetable_cells_hasCourse[6][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[6][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[6][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[6][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[6][5] ? '' : null } </td>
        </tr>
        <tr>
          <td disabled>15-16</td>
          <td> { this.state.timetable_cells_hasCourse[7][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[7][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[7][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[7][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[7][5] ? '' : null } </td>
        </tr>
        <tr>
          <td disabled>16-17</td>
          <td> { this.state.timetable_cells_hasCourse[8][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[8][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[8][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[8][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[8][5] ? '' : null } </td>
        </tr>
        <tr>
          <td disabled>17-18</td>
          <td> { this.state.timetable_cells_hasCourse[9][1] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[9][2] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[9][3] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[9][4] ? '' : null } </td>
          <td> { this.state.timetable_cells_hasCourse[9][5] ? '' : null } </td>
        </tr>
        <tr>

        </tr>
      </TableDragSelect>
      <Button size='sm' id="search-timetable-btn"outline color="danger" onClick={this.handleClick}>Reset</Button>

    </div>;

  handleChange = timetable_cells => this.setState({ timetable_cells });

  handleClick = () => {
    const timetable_cells = [
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
      [false, false, false, false, false, false],
    ];
    this.setState({ timetable_cells });
    console.log(this.state)
  };
}



